.detail__link {
  padding: 5px;
  text-decoration: none;
  color: white;
  background-color: rgb(0, 0, 0);
  transition: all 1s;
}

.delete__button {
  width: 150px;
  height: 40px;
  margin-left: 80px;
  background-color: rgb(201, 200, 200);
  cursor: pointer;
  transition: all 0.5s;

  &:hover {
    background-color: rgb(228, 241, 228);
    transform: scale(110%);
  }
}

.create__post {
  display: flex;
  flex-direction: row;
  gap: 30px;
  justify-content: center;
}

.create__button {
  width: 150px;
  background-color: black;
  color: white;
  cursor: pointer;
  transition: all 1s;

  &:hover{
    font-size: 15px;
    transform: scale(105%);
  }
}

.post__data {
  padding-left: 10px;
  width: 200px;
  height: 50px;
  line-height: 50px;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: 15px;
  background-color: black;
  color: white;
  resize: none;
}

.open__form, .close {
  display: block;
  margin: 50px auto 20px;
  height: 60px;
  width: 300px;
  background-color: rgb(255, 255, 255);
  color: rgb(11, 11, 11);
  cursor: pointer;
  transition: all 1s;

  &:hover {
    background-color: black;
    color: white;
  }
}

.hidden {
  display: none;
}

.modal {
  display: block;
  margin: 0 auto;
  width: 500px;
  border: 2px solid black;
  border-radius: 5px;
  padding: 50px;
}
